// @flow

import React, { Fragment, Component } from "react"
import styled from "styled-components"
import { Toolbar } from "rebass"
import Logo from "components/atoms/Logo"
import Burger from "components/atoms/Burger"

const BaseToolbar = styled(Toolbar)`
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  display: none;
  z-index: 999;
`

const BurgerButton = styled.button.attrs({
  children: <Burger />,
})`
  transition: background-color 0.3s ease-in;
  margin-left: auto;
  width: 48px;
  height: 48px;
  padding: 11px;
  border: none;
  border-radius: 50%;
  box-sizing: border-box;
`

const SmallToolbar = styled(BaseToolbar)`
  @media (max-width: 640px) {
    display: flex;
  }
  ${BurgerButton} {
    background-color: ${props => props.transparent ? "rgba(255,255,255,0.6)" : "rgba(255,255,255,0.9)"};
  }
`

const DefaultToolbar = styled(BaseToolbar)`
  @media (min-width: 641px) {
    display: flex;
  }
  ${props =>
    props.transparent
      ? "background-color: rgba(255,255,255,0.0); padding-top: 6px; align-items: flex-start;"
      : "border-bottom: 1px solid #eee"};

  transition: padding-top 0.3s ease-in, background-color 0.3s ease-in,
    border-bottom 0s linear 0.3s;
`

export default class CustomToolbar extends Component {
  constructor() {
    super(...arguments)
    this.state = { transparent: true }
  }

  componentDidMount() {
    this.handleSizeChange()
    window.addEventListener("resize", this.handleResize)
    window.addEventListener("scroll", this.handleScroll)
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.handleResize)
    window.removeEventListener("scroll", this.handleScroll)
  }

  handleSizeChange = () =>
    this.setState({ transparent: this.isOnTopOfThePage() })

  handleResize = this.handleSizeChange

  handleScroll = this.handleSizeChange

  isOnTopOfThePage = () => {
    const doc = typeof document !== "undefined" ? document.documentElement : {}
    const windowPageOffset =
      typeof window !== "undefined" ? window.pageYOffset : 0
    return (windowPageOffset || doc.scrollTop) - (doc.clientTop || 0) === 0
  }

  render() {
    const { onOpen, ...props } = this.props
    const { transparent } = this.state
    return (
      <Fragment>
        <SmallToolbar
          px={3}
          py={3}
          color="black"
          transparent={transparent}
          {...props}>
          <BurgerButton onClick={onOpen} />
        </SmallToolbar>
        <DefaultToolbar
          px={5}
          py={3}
          color="black"
          bg="white"
          transparent={transparent}
          {...props}
          children={[...props.children, <Logo key="logo" ml="auto" />]}
        />
      </Fragment>
    )
  }
}



