// @flow

import React from "react"
import styled from "styled-components"
import { Box, Flex } from "grid-styled"
import { LargeText, Text } from "components/atoms/typography"
import Container from "components/atoms/Container"
import BlogPostCard from "components/molecules/BlogPostCard"

type Props = {
  id: string,
  title: string,
  description: string,
}

const StyledSection = styled.section`
  padding-top: 96px;
`

export default function SectionWithArticles({
  id,
  title,
  articles,
  description,
}: Props) {
  return (
    <StyledSection id={id}>
      <Container>
        <Flex direction="column" align="center" px={[3, 0]}>
          <LargeText pb={[4, 5]} center is="h3">
            {title}
          </LargeText>
          <Text pb={[3, 4]}>{description}</Text>
        </Flex>
        <Flex wrap mx="-8px" px={[3, 0]}>
          {articles.map((post, index) => (
            <Box key={`post-${index}`} p="8px" w={[1, 1 / 2]}>
              <BlogPostCard post={post} />
            </Box>
          ))}
        </Flex>
      </Container>
    </StyledSection>
  )
}



