// @flow

import React, { Fragment } from "react"
import Navigation from "components/organisms/Navigation"
import Header from "components/organisms/Header"
import Footer from "components/organisms/Footer"
import SectionWithLink from "components/organisms/SectionWithLink"
import SectionWithVideo from "components/organisms/SectionWithVideo"
import SectionWithIcons from "components/organisms/SectionWithIcons"
import SectionWithButton from "components/organisms/SectionWithButton"
import SectionWithImage from "components/organisms/SectionWithImage"
import SectionWithBubbles from "components/organisms/SectionWithBubbles"
import SectionWithArticles from "components/organisms/SectionWithArticles"
import SectionWithCover from "components/organisms/SectionWithCover"
import navigation from "images/icons/navigation.svg"
import ads from "images/icons/ads.svg"
import training from "images/icons/training.svg"
import sales from "images/icons/sales.svg"
import gamification from "images/icons/gamification.svg"
import more from "images/icons/more.svg"
import Link from "components/atoms/Link"

const routes = [
  { title: "HOME", href: "#" },
  { title: "FEATURES", href: "#features" },
  { title: "SOLUTION", href: "#solution" },
  { title: "ABOUT", href: "#about" },
  { title: "CONTACT", href: "#contact" },
]

const IndexPage = ({ data }) => (
  <Navigation routes={routes}>
    <Header
      title="SMARTSHOP"
      subtitle="Make the most of your shopping experience"
      description="Boost your customers’ shopping experience, train employees, optimize sales and manage your store effectively thanks to Smartshop AR solutions!"
      phoneImage={data.phoneImage.resolutions.src}
      backgroundImage={data.backgroundImage.sizes}
    />
    <SectionWithLink
      text="TRY IT FOR FREE"
      linkToAppStore="https://itunes.apple.com/us/app/polidea-smartshop/id1340348804"
    />
    <SectionWithVideo />
    <SectionWithButton
      title="Customize the solution and tailor it to your needs"
      description="Smartshop is proof-of-concept for how AR application might be used for retail. Using our solution as a starting point, we can customize and tailor it to your business needs. Work with us to create something crafted for your store or company!"
      buttonText="Contact us"
    />
    <SectionWithIcons
      id="features"
      title="Top features"
      segments={[
        { title: "Navigation", icon: navigation },
        { title: "Targeted ads", icon: ads },
        { title: "Training", icon: training },
        { title: "Sales optimization", icon: sales },
        { title: "Gamification", icon: gamification },
        { title: "and more", icon: more },
      ]}
    />
    <SectionWithImage
      id="solution"
      title="CUSTOMER"
      description="Smartshop will quickly navigate your customers through the isles to each product from their shopping list."
      textList={[
        "Let your customers plan groceries before getting to the store",
        "Provide pleasant and efficient shopping experience due to indoor navigation",
        "Benefit from personalized promo offers tailored to your customers needs",
      ]}
      imageSource={data.customerImage.resolutions}
      alt="App used by customer"
    />
    <SectionWithImage
      reversed
      title="MANAGER"
      description="Smartshop will help you train your employees, monitor the stock and optimize your sales. It’s your new AR assistant!"
      textList={[
        "Analyze insights from your customers behaviors",
        "Track shelf life of the products ",
        "Train your staff cost-effectively",
      ]}
      imageSource={data.managerImage.resolutions}
      alt="App used by store manager"
    />
    <SectionWithBubbles
      animationDuration={1000}
      delayBetweenPages={15000}
      data={[
        {
          value: 80,
          description: (
            <Fragment>
              of shoppers use a phone inside physical store to find more info
              about a product
              <br />
              <Link
                fontSize={1}
                href="https://www.outerboxdesign.com/web-design-articles/mobile-ecommerce-statistics"
                target="_blank"
                rel="noopener">
                Source
              </Link>
            </Fragment>
          ),
        },
        {
          value: 53,
          description: (
            <Fragment>
              of worldwide shoppers prefer using the retailer’s mobile website
              <br />
              <Link
                fontSize={1}
                href="http://www.businessinsider.com/shopping-app-usage-is-rising-but-retailers-still-have-a-glaring-problem-2016-6?IR=T"
                target="_blank"
                rel="noopener">
                Source
              </Link>
            </Fragment>
          ),
        },
      ]}
    />
    <SectionWithArticles
      id="about"
      title="Want to learn more?"
      articles={[data.chris_post, data.ula_post]}
      description={
        <Fragment>
          Trust us, you’ll love Smartshop—an AR app that will make your shopping
          experience fast and easy. It will quickly navigate you through the
          isles to each product from your shopping list and allow you to plan
          your groceries before you even get to the store! All thanks to the
          pleasant and fun interface. And if that’s not enough, the app will
          also show personalised and targeted promo offers adapted to your
          customers’ preferences. Smartshop was created by Polidea’s team and
          can be further developed and customized, depending on your business
          needs.<br /> <br />Want to learn more? Check our articles!
        </Fragment>
      }
    />
    <SectionWithCover
      id="contact"
      title="Create a full-scale solution with us, tailored to your specific needs!"
      buttonText="Contact us"
      backgroundImage={data.contactBackgroundImage.sizes}
    />
    <Footer description="With 8 years of experience in delivering mobile solutions covering design and development, we are extending our services with future technologies such as Virtual and Augmented Reality. We believe that VR and AR hold great potential and will shape the future business scene." />
  </Navigation>
)

export default IndexPage

export const query = graphql`
  query IndexPageQuery {
    backgroundImage: imageSharp(id: { regex: "/bg/" }) {
      sizes(maxWidth: 1280, quality: 95) {
        ...GatsbyImageSharpSizes_withWebp
      }
    }
    phoneImage: imageSharp(id: { regex: "/hand_screen/" }) {
      resolutions(width: 279) {
        src
      }
    }
    customerImage: imageSharp(id: { regex: "/customer/" }) {
      resolutions(
        width: 220
        traceSVG: {
          turnPolicy: TURNPOLICY_MAJORITY
          turdSize: 250
          alphaMax: 1
          optCurve: true
          optTolerance: 0.4
          blackOnWhite: true
          color: "#038c3e"
          threshold: 220
        }
      ) {
        ...GatsbyImageSharpResolutions_withWebp_tracedSVG
      }
    }
    managerImage: imageSharp(id: { regex: "/manager/" }) {
      resolutions(
        width: 220
        traceSVG: {
          turnPolicy: TURNPOLICY_MAJORITY
          turdSize: 250
          alphaMax: 1
          optCurve: true
          optTolerance: 0.4
          blackOnWhite: true
          color: "#038c3e"
          threshold: 220
        }
      ) {
        ...GatsbyImageSharpResolutions_withWebp_tracedSVG
      }
    }
    contactBackgroundImage: imageSharp(id: { regex: "/contact/" }) {
      sizes(maxWidth: 1280, quality: 95) {
        ...GatsbyImageSharpSizes_withWebp
      }
    }

    chris_post: markdownRemark(
      id: {
        regex: "/6-ways-to-boost-retail-sales-with-arvr-and-a-new-smartshop-ar-app/"
      }
    ) {
      timeToRead
      fields {
        slug
      }
      frontmatter {
        tags
        category
        title
        description
        author {
          id
          short_id
          fullname
        }
        cover {
          regularCover: regular {
            childImageSharp {
              sizes(
                maxWidth: 560
                maxHeight: 320
                cropFocus: CENTER
                quality: 70
                toFormat: JPG
              ) {
                ...GatsbyImageSharpSizes_withWebp
              }
            }
          }
        }
      }
    }
    ula_post: markdownRemark(
      id: {
        regex: "/trendwatching-3digital-transformation-in-retail-ar-solutions/"
      }
    ) {
      timeToRead
      fields {
        slug
      }
      frontmatter {
        tags
        category
        title
        description
        author {
          id
          short_id
          fullname
        }
        cover {
          regularCover: regular {
            childImageSharp {
              sizes(
                maxWidth: 560
                maxHeight: 320
                cropFocus: CENTER
                quality: 70
                toFormat: JPG
              ) {
                ...GatsbyImageSharpSizes_withWebp
              }
            }
          }
        }
      }
    }
  }
`



