// @flow

import React from "react"
import styled from "styled-components"
import Img from "gatsby-image"
import { Box } from "grid-styled"
import { Heading } from "components/atoms/typography"
import Container from "components/atoms/Container"
import Button from "components/molecules/Button"
import ContactFragment from "components/molecules/ContactFragment"

type Props = {
  id: string,
  title: string,
  backgroundImage: string,
  buttonText: string,
}

const StyledSection = styled.section`
  padding-top: 96px;
  padding-bottom: 32px;
`

const ShadowHeading = styled(Heading)`
  @media (min-width: 641px) {
    text-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
  }
`

const renderButton = () => (
  <Button color={["white", "green"]} bg={["green", "white"]}>
    CONTACT US
  </Button>
)

const TextBox = ({ title, ...props }) => (
  <Box key="text" pt={3} pb={3} {...props}>
    <ShadowHeading
      pb={[4, 5]}
      px={[null, 6]}
      is="h3"
      color={["green", "white"]}
      center>
      {title}
    </ShadowHeading>
    <ContactFragment renderButton={renderButton} />
  </Box>
)

const StyledTextBox = styled(TextBox)`
  text-align: center;
  @media (max-width: 640px) {
    border-radius: 18px;
    border: solid 2px #038c3e;
  }
`

const Cover = styled(Img)`
  @media (max-width: 640px) {
    display: none;
  }
`

const Relative = styled.div`
  position: relative;
  overflow: hidden;
  z-index: 1;
`

export default function SectionWithCover({
  id,
  title,
  backgroundImage,
  buttonText,
}: Props) {
  return (
    <StyledSection id={id}>
      <Relative>
        <Cover
          style={{ position: "absolute", top: 0, left: 0, right: 0, bottom: 0 }}
          sizes={backgroundImage}
        />
        <Container py={[3, 6]} px={2}>
          <Relative>
            <StyledTextBox px={[4, 5]} title={title} buttonText={buttonText} />
          </Relative>
        </Container>
      </Relative>
    </StyledSection>
  )
}



