// @flow

import React from "react"
import styled from "styled-components"
import { Flex } from "grid-styled"
import { Heading, Text } from "components/atoms/typography"
import Theme from "components/atoms/Theme"
import Image from "components/atoms/Image"
import Container from "components/atoms/Container"

type SectionWithImageProps = {
  id: string,
  reversed: boolean,
  title: string,
  description: string,
  textList: Array<string>,
  imageSource: string,
  alt: string,
}

const StyledSection = styled.section`
  box-sizing: content-box;
  padding-top:64px;
  background-color: ${Theme.colors.whitish};
  ${StyledSection} + section {
    margin-top: -32px;
  }

`

const TextBox = ({ title, description, textList, ...props }) => (
  <Flex
    key="text"
    pt={4}
    pb={2}
    width={[1, 1 / 2]}
    direction="column"
    {...props}>
    <Heading color="green" pb={4}>
      {title}
    </Heading>
    <Text pb={2}>{description}</Text>
    <ul>
      {textList.map((text, index) => (
        <li key={`list-${index}`}>
          <Text>{text}</Text>
        </li>
      ))}
    </ul>
  </Flex>
)

const ImageBox = ({ imageSource, alt, ...props }) => (
  <Flex key="image" width={[1, 1 / 2]} justify="center" {...props}>
    <Image resolutions={imageSource} alt={alt} />
  </Flex>
)

export default function SectionWithImage({
  id,
  reversed,
  title,
  description,
  textList,
  imageSource,
  alt,
}: SectionWithImageProps) {
  return (
    <StyledSection id={id}>
      <Container>
        <Flex
          py={[4, 5]}
          direction={["column-reverse", reversed ? "row-reverse" : "row"]}>
          <TextBox
            title={title}
            description={description}
            textList={textList}
            pl={4}
            pr={4}
          />
          <ImageBox
            imageSource={imageSource}
            pl={4}
            pr={4}
            alt={alt}
          />
        </Flex>
      </Container>
    </StyledSection>
  )
}

SectionWithImage.defaultProps = {
  reversed: false,
}



