// @flow

import React, { Component, Fragment } from "react"
import { Fixed } from "rebass"
import Toolbar from "components/molecules/Toolbar"
import ToolbarButton from "components/molecules/ToolbarButton"
import Drawer from "components/molecules/Drawer"
import NavLink from "components/atoms/NavLink"

export default class Navigation extends Component {
  constructor() {
    super(...arguments)
    this.state = {
      open: false,
    }
  }

  handleOpen = () => this.setState(prev => ({ open: !prev.open }))

  handleClose = () => this.setState({ open: false })

  render() {
    const { routes } = this.props
    const { open } = this.state

    return (
      <Fragment>
        <div>{this.props.children}</div>
        {open && <Fixed top right bottom left onClick={this.handleOpen} />}
        <Toolbar onOpen={this.handleOpen}>
          {routes.map(route => (
            <ToolbarButton
              key={`route-${route.href}`}
              children={route.title}
              href={route.href}
            />
          ))}
        </Toolbar>
        <Drawer open={open} onClose={this.handleClose}>
          {routes.map(route => (
            <NavLink
              key={`route-${route.href}`}
              children={route.title}
              href={route.href}
              onClick={this.handleClose}
            />
          ))}
        </Drawer>
      </Fragment>
    )
  }
}



