// @flow

import React from "react"
import { Image } from "rebass"
import styled from "styled-components"
import logo from "images/logo.svg"

const Logo = styled(Image)`
  height: 37px;
`

export default props => <Logo src={logo} alt="Polidea’s logo" {...props} />



