// @flow

import React from "react"
import Image from "gatsby-image"
import { Flex, Box } from "grid-styled"
import {
  Small,
  Card,
  Heading,
  Text,
  AvatarSet,
  Avatar,
} from "@corgi/components"
import { CategoryLabel, TagLabel } from "@corgi/frontend"

function PostCover({ cover, ...props }) {
  return (
    <Box mt="-8px" mx="-16px" mb="8px" {...props}>
      <Image sizes={cover.regularCover.childImageSharp.sizes} />
    </Box>
  )
}

export default function BlogPostCard({ post, ...props }) {
  const link = `https://polidea.com${post.fields.slug}`
  const { timeToRead } = post
  const { category, tags, title, description, author } = post.frontmatter
  return (
    <a href={link}>
      <Card {...props}>
        <PostCover cover={post.frontmatter.cover} />
        <Flex
          itemProp="keywords"
          align="center"
          justify="space-between"
          pb="4px">
          <CategoryLabel category={category} />
          <Small align="right">{timeToRead}min read</Small>
        </Flex>
        <Flex itemProp="keywords">
          {tags.map(tag => <TagLabel key={`tag-${tag}`} tag={tag} />)}
        </Flex>
        <Heading>{title}</Heading>
        <Text itemProp="description" mb="24px">
          {description}
        </Text>
        <Flex align="center" itemProp="author">
          <AvatarSet pr="8px">
            {author.map(author => (
              <Avatar
                key={`author-${author.short_id}`}
                src={`https://polidea.com/img/people/${author.short_id}.jpg`}
              />
            ))}
          </AvatarSet>
          <Small>
            {author.length === 1
              ? getFirstName(author[0].fullname)
              : `${author.length} authors`}
          </Small>
        </Flex>
      </Card>
    </a>
  )
}

const getFirstName = fullname => fullname.split(" ")[0]



