// @flow

import React from "react"
import styled from "styled-components"
import { Flex } from "grid-styled"
import { Link } from "rebass"
import fb from "images/icons/fb.svg"
import twitter from "images/icons/twitter.svg"
import linkedin from "images/icons/linkedin.svg"
import github from "images/icons/github.svg"
import instagram from "images/icons/instagram.svg"

const SocialLink = styled(Link)`
  display: block;
`

const Icon = styled.img`
  display: block;
  @media (max-width: 640px) {
    width: 32px;
  }
`

export default function SocialIcons(props) {
  return (
    <Flex align="center" justify="center" {...props}>
      <SocialLink
        p={2}
        m={1}
        aria-label="Polidea’s Facebook page"
        href="https://www.facebook.com/Polidea.Software"
        target="_blank"
        rel="noopener">
        <Icon src={fb} alt="Facebook icon"/>
      </SocialLink>
      <SocialLink
        p={2}
        m={1}
        aria-label="Polidea’s Twitter profile"
        href="https://twitter.com/polidea"
        target="_blank"
        rel="noopener">
        <Icon src={twitter} alt="Twitter icon"/>
      </SocialLink>
      <SocialLink
        p={2}
        m={1}
        aria-label="Polidea’s Linkedin profile"
        href="https://www.linkedin.com/company/polidea"
        target="_blank"
        rel="noopener">
        <Icon src={linkedin} alt="Linkedin icon"/>
      </SocialLink>
      <SocialLink
        p={2}
        m={1}
        aria-label="Polidea’s Github organization"
        href="https://github.com/Polidea"
        target="_blank"
        rel="noopener">
        <Icon src={github} alt="Github icon"/>
      </SocialLink>
      <SocialLink
        p={2}
        m={1}
        aria-label="Polidea’s Instagram profile"
        href="https://instagram.com/polidea"
        target="_blank"
        rel="noopener">
        <Icon src={instagram} alt="Instagram icon"/>
      </SocialLink>
    </Flex>
  )
}



