export const _CategoryGradient = ({ theme, category }) => {
  return {
    all:          `${theme.colors.warmGrey}, ${theme.colors.warmGrey}`,
    engineering:  `${theme.colors.nastyGreen}, ${theme.colors.nastyGreen} 25%, ${theme.colors.greenyBlue} 75%, ${theme.colors.greenyBlue}`,
    design:       `${theme.colors.greenyBlue}, ${theme.colors.greenyBlue} 25%, ${theme.colors.waterBlue} 45%, ${theme.colors.cornflowerBlue} 75%, ${theme.colors.cornflowerBlue}`,
    business:     `${theme.colors.iris}, ${theme.colors.iris} 25%, ${theme.colors.deepRose} 75%, ${theme.colors.deepRose}`,
    news:         `${theme.colors.darkPink}, ${theme.colors.darkPink} 25%, ${theme.colors.tomato} 75%, ${theme.colors.tomato}`,
    resources:    `${theme.colors.dustyOrange}, ${theme.colors.dustyOrange} 25%, ${theme.colors.squash} 75%, ${theme.colors.squash}`
  }[category]
}



