// @flow

import styled from "styled-components"
import { NavLink } from "rebass"
import { small } from "components/atoms/typography/Styles"
import Theme from "components/atoms/Theme"

export default styled(NavLink)`
  ${small};
  &:hover {
    color: ${Theme.colors.green};
    background-color: transparent;
  }
  color: ${props => (props.active ? Theme.colors.green : Theme.colors.black)};
  background-color: transparent;
  ${props =>
    props.active
      ? `border-bottom: 4px solid ${Theme.colors.green}; margin-bottom: -4px;`
      : ""};
`



