// @flow

import React from "react"
import styled from "styled-components"
import { Button as ReButton } from "rebass"
import { subhead } from "components/atoms/typography/Styles"

const StyledButton = styled(ReButton)`
  border-radius: 18px;
  ${subhead};
`

export default function Button({ children, ...props }) {
  return (
    <StyledButton bg="green" px={5} py={3} {...props}>
      {children.toUpperCase()}
    </StyledButton>
  )
}



