// @flow

import React from "react"
import { Flex } from "grid-styled"
import { LargeText } from "components/atoms/typography"
import { SingleColorInputWithButton } from "@corgi/components"

export default function ContactForm() {
  return (
    <form method="POST" action="https://formspree.io/hello@polidea.com">
      <Flex align="center" py="48px" px={["16px", "32px"]} direction="column">
        <LargeText pb="48px" textAlign="center" align="center">
          Leave your email if you want to get more information.
        </LargeText>
        <Flex justify="center" w={[1, "420px"]}>
          <SingleColorInputWithButton
            color="#038c3e"
            type="email"
            name="email"
            placeholder="enter your e-mail">
            Send
          </SingleColorInputWithButton>
        </Flex>
      </Flex>
      <input
        type="hidden"
        name="_subject"
        value="[Smartshop] New lead from landing"
      />
      <input
        type="hidden"
        name="_next"
        value="//smartshop.polidea.com/#contact-form-thanks"
      />
    </form>
  )
}



