// @flow

import React from "react"
import Circle from "components/atoms/Circle"
import Counter from "components/atoms/Counter"

export default function AnimatedBubble({
  suffix,
  from,
  to,
  duration,
  ...props
}) {
  return (
    <Counter from={from} to={to} duration={duration}>
      {value => (
        <Circle value={value} size={100} color="white" {...props}>
          {value.toFixed(0)}
          {suffix}
        </Circle>
      )}
    </Counter>
  )
}



