// @flow

import React, { Component } from "react"
import styled from "styled-components"
import { Flex } from "grid-styled"
import { Text } from "components/atoms/typography"
import Theme from "components/atoms/Theme"
import Container from "components/atoms/Container"
import AnimatedBubble from "components/molecules/AnimatedBubble"

type Datum = {
  value: number,
  description: Component,
}

type Props = {
  data: Array<Datum>,
  delayBetweenPages: number,
  animationDuration: number,
}

type State = {
  currentIndex: number,
}

const StyledSection = styled.section`
  background-color: ${Theme.colors.green};
  margin-top: 0 !important;
`

const BaseStat = ({ id, value, description, animationDuration, ...props }) => (
  <Flex key={`stat-${id}`} width={[1, 1 / 2]} pr={4} {...props}>
    <Flex mr={4} align="center">
      <AnimatedBubble to={value} suffix="%" duration={animationDuration} />
    </Flex>
    <Flex flex="1" align="center">
      <Text color="white">{description}</Text>
    </Flex>
  </Flex>
)

const SmallStat = styled(BaseStat)`
  display: none;
  @media (max-width: 640px) {
    display: flex;
  }
`

const DefaultStat = styled(BaseStat)`
  display: none;
  @media (min-width: 641px) {
    display: flex;
  }
`

class DefaultSectionWithBubbles extends Component {
  state: State

  constructor() {
    super(...arguments)
    this.state = {
      currentIndex: 0,
    }
  }

  componentDidMount() {
    this.tickInterval = setInterval(this.update, this.props.delayBetweenPages)
  }

  componentWillMount() {
    clearInterval(this.tickInterval)
  }
  update = () =>
    this.setState(prev => ({
      currentIndex:
        prev.currentIndex + 1 >= this.props.data.length / 2
          ? 0
          : prev.currentIndex + 1,
    }))

  groupInPairs(data) {
    return data.reduce((pairs, _, index, array) => {
      if (index % 2 === 0) {
        pairs.push(array.slice(index, index + 2))
      }
      return pairs
    }, [])
  }

  render() {
    const { data, animationDuration } = this.props
    const { currentIndex } = this.state

    const pairs = this.groupInPairs(
      data.map((d, index) => ({ id: index, ...d }))
    )

    return pairs[currentIndex].map((data, index) => (
      <DefaultStat
        key={`stat-${index}`}
        animationDuration={animationDuration}
        {...data}
      />
    ))
  }
}

class SmallSectionWithBubbles extends Component {
  state: State
  
  constructor() {
    super(...arguments)
    this.state = {
      currentIndex: 0,
    }
  }

  componentDidMount() {
    this.tickInterval = setInterval(this.update, this.props.delayBetweenPages)
  }

  componentWillMount() {
    clearInterval(this.tickInterval)
  }
  render() {
    const { data, animationDuration } = this.props
    const { currentIndex } = this.state

    const currentData = data[currentIndex]
    return (
      <SmallStat
        key={`stat-${currentIndex}`}
        animationDuration={animationDuration}
        {...currentData}
      />
    )
  }

  update = () =>
    this.setState(prev => ({
      currentIndex:
        prev.currentIndex + 1 >= this.props.data.length
          ? 0
          : prev.currentIndex + 1,
    }))
}

export default class SectionWithBubbles extends Component {
  props: Props

  render() {
    const { data, animationDuration, delayBetweenPages } = this.props

    return (
      <StyledSection>
        <Container>
          <Flex px={4} py={5}>
            <SmallSectionWithBubbles
              animationDuration={animationDuration}
              delayBetweenPages={delayBetweenPages}
              data={data}
            />
            <DefaultSectionWithBubbles
              delayBetweenPages={delayBetweenPages}
              animationDuration={animationDuration}
              data={data}
            />
          </Flex>
        </Container>
      </StyledSection>
    )
  }
}



