// @flow

import React from "react"
import styled from "styled-components"
import { Flex } from "grid-styled"
import { Heading } from "components/atoms/typography"
import Theme from "components/atoms/Theme"
import Container from "components/atoms/Container"
import AppStore from "components/atoms/AppStore"

const StyledSection = styled.section`
  background-color: ${Theme.colors.green};
`

type SectionWithLinkProps = { text: string, linkToAppStore: string }

export default function SectionWithLink(props: SectionWithLinkProps) {
  return (
    <StyledSection>
      <Container>
        <Flex py={[4, 5]} direction={["column", "row"]}>
          <Flex
            pl={[4, 3]}
            pr={[4, 5]}
            width={[1, 1 / 2]}
            align="center"
            justify={["center", "flex-end"]}>
            <Heading color="white">{props.text.toUpperCase()}</Heading>
          </Flex>
          <Flex
            pl={[4, 5]}
            pr={[4, 3]}
            py={3}
            width={[1, 1 / 2]}
            align="center"
            justify={["center", "flex-start"]}>
            <AppStore
              href={props.linkToAppStore}
              target="_blank"
              rel="noopener"
            />
          </Flex>
        </Flex>
      </Container>
    </StyledSection>
  )
}



