// @flow

import React from "react"
import styled from "styled-components"
import { Box, Flex } from "grid-styled"
import { LargeText, Subhead } from "components/atoms/typography"
import Container from "components/atoms/Container"

type SegmentT = {
  title: string,
  icon: string,
}

type SectionWithIconsProps = {
  id: string,
  title: string,
  segments: Array<SegmentT>,
}

const IconBox = styled(Flex)`
  width: 280px;
  height: 220px;
  border-radius: 18px;
  align-items: center;
  justify-content: center;
  background-color: #f1f1f1;
`

const Segments = styled(Flex)`
  @media (min-width: 641px) {
    overflow-x: hidden;
  }
  overflow-x: scroll;
`

const Segment = ({ icon, title, ...props }) => (
  <Box px={[2, 5]} py={3} {...props}>
    <IconBox>
      <img src={icon} alt={title}/>
    </IconBox>
    <Subhead p={3}>{title}</Subhead>
  </Box>
)

export default function SectionWithIcons({
  id,
  title,
  segments,
}: SectionWithIconsProps) {
  return (
    <section id={id}>
      <Container>
        <LargeText pt={96} pb={[4, 4]} center is="h3">
          {title}
        </LargeText>
        <Segments
          pb={3}
          justify={[null, "center"]}
          wrap={[false, true]}>
          {segments.map((segment, index) => (
            <Segment key={`segment-${index}`} {...segment} />
          ))}
        </Segments>
      </Container>
    </section>
  )
}



