// @flow

import React from "react"
import Img from "gatsby-image"
import styled from "styled-components"
import { Flex, Box } from "grid-styled"
import { Heading, LargeText, Text } from "components/atoms/typography"
import AnimatedTrail from "components/molecules/AnimatedTrail"

type HeaderProps = {
  title: string,
  subtitle: string,
  description: string,
  backgroundImage: string,
}

const StyledHeader = styled.header`
  width: 100%;
  position: relative;
  overflow: hidden;
`

const Container = styled.div`
  position: relative;
  margin: 0 auto;
`

const TextBox = styled(Box)`
  position: relative;
  z-index: 2;
  @media (max-width: 640px) {
    background-color: rgba(255, 255, 255, 0.6);
    margin-top: -60px;
  }
`

const Content = styled(Box)`
  background-color: white;
  @media (min-width: 641px) {
    min-height: 600px;
    background-color: transparent;
  }
`

const Relative = styled.div`
  position: relative;
  height: 100%;
  margin: 8px;
  z-index: 1;
`

const Trail = styled(AnimatedTrail)`
  @media (max-width: 640px) {
    margin-top: -15%;
    padding-left: 20%;
  }
  @media (min-width: 641px) {
    position: absolute;
    margin-top: 10%;
    padding-top: 10%;
    bottom: 0;
    left: -15%;
  }
`

export default function Header(props: HeaderProps) {
  return (
    <StyledHeader>
      <Container>
        <Img
          style={{ position: "absolute", top: 0, left: 0, right: 0, bottom: 0 }}
          sizes={props.backgroundImage}
        />
        <Flex direction={["column-reverse", "row"]}>
          <TextBox width={[1, 1 / 2]} pl={[0, 2]}>
            <Heading color="green" pt={[3, 6]} pb={[3, 4]} px={[3, 5]}>
              {props.title}
            </Heading>
            <Content px={[3, 5]} pt={[3, 0]} pb={[0, 256]}>
              <LargeText pb={4}>{props.subtitle}</LargeText>
              <Text pb={4}>{props.description}</Text>
            </Content>
          </TextBox>
          <Box p={2} width={[1, 1 / 2]}>
            <Relative>
              <Trail
                imageSource={props.phoneImage}
                path={[
                  { x0: 270, y0: 330, x1: 405, y1: 225, count: 4 },
                  { x0: 338, y0: 240, x1: 0, y1: 150, count: 11 },
                ]}
              />
            </Relative>
          </Box>
        </Flex>
      </Container>
    </StyledHeader>
  )
}



