// @flow

import React from "react"
import styled from "styled-components"
import { Box } from "grid-styled"
import Container from "components/atoms/Container"

const StyledSection = styled.section`
  padding-top: 96px;
  padding-bottom: 32px;
  text-align: center;
`

const VideoBox = styled(Box)`
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  padding-top: 25px;
  height: 0;

  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
`

export default function SectionWithVideo() {
  return (
    <StyledSection>
      <Container px={[3, 5]}>
        <VideoBox>
          <iframe
            width="800"
            height="450"
            src="https://www.youtube.com/embed/ljaSIrutgx4?rel=0&amp;showinfo=0"
            frameBorder="0"
            allow="autoplay; encrypted-media"
            allowFullScreen
          />
        </VideoBox>
      </Container>
    </StyledSection>
  )
}



