// @flow

import React from "react"
import styled from "styled-components"
import { Box } from "grid-styled"
import { Heading } from "components/atoms/typography"

const StyledCircle = styled.circle`
  fill: transparent;
  stroke: ${props => props.color};
`

const Container = styled(Box)`
  position: relative;
  width: ${props => props.size}px;
  height: ${props => props.size}px;
`

const StyledHeading = styled(Heading)`
  position: absolute;
  text-align: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
`

export default function Circle({ value, size, color, children }) {
  const border = 4
  const r = (size - border) / 2
  const c = Math.PI * (r * 2)
  const pct = c * ((100 - value) / 100)

  return (
    <Container size={size}>
      <svg width={size} height={size} viewBox="0 0 100 100">
        <StyledCircle
          r={r}
          cx="50"
          cy="50"
          strokeWidth={border}
          strokeDasharray={c}
          strokeDashoffset="0"
          color="rgba(255, 255, 255, 0.5)"
        />
        <StyledCircle
          r={r}
          cx="50"
          cy="50"
          strokeWidth={border}
          strokeDasharray={c}
          strokeDashoffset={pct}
          color={color}
        />
      </svg>
      <StyledHeading color="white" is="span">{children}</StyledHeading>
    </Container>
  )
}



