// @flow

import React from "react"
import styled from "styled-components"
import { Flex } from "grid-styled"
import { Drawer } from "rebass"
import { Small } from "components/atoms/typography"
import Logo from "components/atoms/Logo"
import Link from "components/atoms/Link"
import Close from "components/atoms/Close"

const StyledDrawer = styled(Drawer)`
  ${props => (!props.open ? "transform: translateX(100%)" : null)};
  max-width: 320px;
  top: 0;
  left: auto;
  right: 0;
  bottom: 0;
  box-sizing: border-box;
  z-index: 9999;

  @media (min-width: 641px) {
    display: none;
  }
`

const Header = styled(Flex)`
  justify-content: space-between;
  background-color: white;
`

export default ({ open, children, onClose, ...props }) => (
  <StyledDrawer open={open} color="white" bg="green" {...props}>
    <Header pl={5} pr={2} py={3}>
      <Logo />
      <Close onClick={onClose} />
    </Header>
    <Flex
      style={{
        position: "relative",
      }}
      direction="column"
      px={5}
      pt={4}
      m={0}>
      {children}
      <Small p={2} my={3}>
        Made by{" "}
        <Link
          href="https://www.polidea.com"
          target="_blank"
          rel="noopener"
          onClick={onClose}>
          Polidea
        </Link>
      </Small>
    </Flex>
  </StyledDrawer>
)



