// @flow

import React, { Component } from "react"
import styled from "styled-components"

const Circle = styled.circle`
  opacity: ${props => props.opacity};
  transition: all 0.09s ease-in;
`

class TrailPoint extends Component {
  shouldComponentUpdate(nextProps) {
    const { x, y, index, scale } = this.props
    const { x: newX, y: newY, index: newIndex, scale: newScale } = nextProps
    return x !== newX || y !== newY || index !== newIndex || scale !== newScale
  }

  render() {
    const { x, y, index, scale, ...props } = this.props
    return (
      <Circle
        cx={x}
        cy={y}
        opacity={1 - index * 0.04}
        zIndex={index}
        r={15 * (1 - index * 0.05) * scale}
        fill="rgb(214, 58, 92)"
        {...props}
      />
    )
  }
}

const Svg = styled.svg`
  max-height: 100%;
`

type Line = {
  x0: number,
  y0: number,
  x1: number,
  y1: number,
  count: number,
}

type Props = {
  path: Array<Line>,
}

type State = {
  progress: number,
}

export default class AnimatedTrail extends Component<Props, State> {
  constructor() {
    super(...arguments)
    this.state = { progress: 0 }
  }

  componentDidMount() {
    this.tickInterval = setInterval(this.update, 100)
  }

  componentWillUnmount() {
    this.tickInterval && clearInterval(this.tickInterval)
  }

  update = () =>
    this.setState(prev => ({
      progress: prev.progress + 1 > this.pathLength() ? 0 : prev.progress + 1,
    }))

  pathLength = () =>
    this.props.path.reduce((length, line) => length + line.count, 0)

  render() {
    const { path } = this.props
    let pointIndex = -1
    const pathElements = path.map(line => {
      return Array.apply(null, Array(line.count)).map((_, index) => {
        const dx = (line.x1 - line.x0) * index / line.count
        const dy = (line.y1 - line.y0) * index / line.count
        const x = line.x0 + dx
        const y = line.y0 + dy
        pointIndex++
        const distanceFromActive = Math.abs(
          pointIndex - this.state.progress - 1
        )
        const scale = Math.max(1, 1.4 - Math.min(distanceFromActive, 3) * 0.2)
        return (
          <TrailPoint
            key={`point-${pointIndex}`}
            index={pointIndex}
            scale={scale}
            x={x}
            y={y}
          />
        )
      })
    })
    return (
      <Svg
        className={this.props.className}
        xmlns="http://www.w3.org/2000/svg"
        xmlnsXlink="http://www.w3.org/1999/xlink"
        preserveAspectRatio="xMinYMid slice"
        viewBox="0 0 640 800">
        {pathElements}
        <image
          xlinkHref={this.props.imageSource}
          href={this.props.imageSource}
          width="280px"
          height="500px"
          x="0"
          y="300"
        />
      </Svg>
    )
  }
}



