// @flow

import React, { Fragment } from "react"
import styled from "styled-components"
import ContactForm from "components/molecules/ContactForm"
import ContactThanks from "components/molecules/ContactThanks"
import { Modal } from "@corgi/components"

const CustomModal = styled(Modal)`
  width: 800px;
  border-radius: 13px;
`

export default function ContactFragment({ renderButton }) {
  return (
    <Fragment>
      <CustomModal modalId="contact-form" OpenModalButton={renderButton}>
        <ContactForm />
      </CustomModal>
      <CustomModal modalId="contact-form-thanks">
        <ContactThanks />
      </CustomModal>
    </Fragment>
  )
}



