// @flow

export const linear = (time, initialValue, change, totalDuration) =>
  (change - initialValue) * (time / totalDuration)

export const easeInQuad = (time, initialValue, change, totalDuration) =>
  change * (time /= totalDuration) * time + initialValue

export const easeInCubic = (time, initialValue, change, totalDuration) =>
  change * (time /= totalDuration) * time * time + initialValue

export const easeOutCubic = (time, initialValue, change, totalDuration) =>
  change * ((time = time / totalDuration - 1) * time * time + 1) + initialValue



