// @flow

import React, { Component } from "react"
import * as Animations from "components/atoms/Animations"

export default class Counter extends Component {
  static defaultProps = {
    from: 0,
    to: 100,
    duration: 5000,
  }

  constructor(props) {
    super(props)
    this.state = {
      currentValue: props.from,
    }
  }

  componentDidMount() {
    this._start = new Date().getTime()
    this.animate()
  }

  render() {
    const renderedChildren = this.props.children(this.state.currentValue)
    return renderedChildren && React.Children.only(renderedChildren)
  }

  animate = () => {
    requestAnimationFrame(() => {
      const now = new Date().getTime()
      const dt = now - this._start
      if (dt < this.props.duration) {
        this.setState(
          () => ({
            currentValue: Animations.linear(
              dt,
              this.props.from,
              this.props.to,
              this.props.duration
            ),
          }),
          () => this.animate()
        )
      } else {
        this.setState({
          currentValue: this.props.to,
        })
      }
    })
  }
}



