// @flow

import React from "react"
import { Flex } from "grid-styled"
import { LargeText } from "components/atoms/typography"
import Success from "components/atoms/Success"

export default function() {
  return (
    <Flex align="center" py="48px" px={["16px", "32px"]} direction="column">
      <LargeText align="center" textAlign="center" pt="24px" pb="48px">
        Thanks!<br />
        We will contact you shortly.
      </LargeText>
      <Success/>
    </Flex>
  )
}



