// @flow

import styled from "styled-components"
import Img from "gatsby-image"

export default styled(Img)`
  flex: 0 0 auto;
  max-width: 100%;
  max-height: 100%;
  align-self: flex-start;
  object-fit: scale-down;
  backface-visibility: hidden;
`



